<template>
  <div class="pay">
    <van-nav-bar
      title="订单结算"
      left-text="返回"
      left-arrow
      @click-left="black"
    />
    <BgBox>
      <van-tabs v-model="active" @click="toggleIndex">
      <van-tab :title="ver.title" v-for="(ver, num) in tabList" :key="num" >
 
                <div class="shop-item" v-for="(item, index) in orderData" :key="index">
                  <div class="shop-item-info">
                  <div class="orderNum">{{item.oid}}</div>
                  <div class="item-list" v-for="(v,i) in item.data" :key="i">
                    <div class="shop-item-img">
                        <img :src="v.smallImg" alt="" />
                    </div>
                    <div class="shop-item-desc">
                        <div class="shop-name">
                        <div>{{ v.name }}</div>
                        <div class="condit">{{ v.rule }}</div>
                    </div>
                        <div class="ename">{{ v.enname }}</div>
                        <div class="shopPrice-box">
                        <div class="price">￥{{ v.price }}</div>
                        <div class="count">x{{ v.count }}</div>
                    </div>

                </div>
                
                </div>
                <div class="receive" v-show="currentIndex !== 2" @click="receive(item,index)">确认收货</div>
                <div class="del" v-show="currentIndex == 2" @click="delOrder(item,index)">删除订单</div>
                <div class="total">
                <div class="total-count">共计 {{ item.count }} 件商品</div>
                <div class="total-price">
                合计：￥ <span>{{ item.total | decimal }}</span>
                </div>
            </div>
            </div>
            </div>

      </van-tab>
    </van-tabs>
    </BgBox>
    
  </div>
</template>

<script>
import "../assets/less/pay.less";
import BgBox from "../components/BgBox.vue"
export default {
  name: "Pay",
  components: {
    BgBox
  },
  data() {
    return {
      active: 0,
      orderData: [],
      tabList: [
          {title: '全部'},
          {title: '待收货'},
          {title: '已收货'}
      ],
      currentIndex: 0
    };
  },
  created() {
    this.getOrder(0);
  },
  methods: {
    black() {
      this.$router.go(-1);
    },
    toggleIndex(name){
        this.currentIndex = name;
        this.getOrder(name);
    },
    // 查询订单
    getOrder(currentIndex) {

      let tokenString = localStorage.getItem("token");
      if (!tokenString) {
        this.$toast("请登录");
        return this.$router.push({ name: "Login" });
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });
      this.axios({
        method: "GET",
        url: "/findOrder",
        params: {
          appkey: this.appkey,
          tokenString,
          status: currentIndex
        },
      })
        .then((res) => {
          
          this.$toast.clear();
          if (res.data.code == 70000) {
            //按照订单编号oid进行分类

            let orderDatas = [];
            res.data.result.map((v) => {
              //查找orderDatas的订单数据，如果找到，则将该商品存放在当前订单里面，累加当前订单的商品数量和当前订单的总金额
              for (let i = 0; i < orderDatas.length; i++) {
                if (orderDatas[i].oid == v.oid) {
                  orderDatas[i].data.push(v);
                  orderDatas[i].count += v.count;
                  orderDatas[i].total += v.count * v.price;
                  return;
                }
              }
              //如果找不到当前订单数据，则初始化当前订单数据
              let o = {};
              o.oid = v.oid;
              o.count = v.count;
              o.total = v.count * v.price;
              o.status = v.status;
              o.date = v.createdAt;
              o.data = [v];
              orderDatas.push(o);
            });

            this.orderData = orderDatas;
            
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //确认收货
    receive(item,index){
          let tokenString = localStorage.getItem("token");
        if (!tokenString) {
            this.$toast("请登录");
            return this.$router.push({ name: "Login" });
        }
        this.$toast.loading({
            message: "加载中...",
            forbidClick: true,
            duration: 0,
        });
        this.axios({
            method: "POST",
            url: "/receive",
            data: {
                appkey: this.appkey,
                tokenString,
                oid: item.oid
            },
        }).then(res => {
            this.$toast.clear();
            if(res.data.code == 80000){
                if(this.currentIndex == 1){
                    this.orderData.splice(index, 1);
                }
                item.status = 2;
            }
            
            
            this.$toast(res.data.msg);
        })
    },
    //删除订单
    delOrder(item,index){
          let tokenString = localStorage.getItem("token");
        if (!tokenString) {
            this.$toast("请登录");
            return this.$router.push({ name: "Login" });
        }
        this.$toast.loading({
            message: "加载中...",
            forbidClick: true,
            duration: 0,
        });
        this.axios({
            method: "POST",
            url: "/removeOrder",
            data: {
                appkey: this.appkey,
                tokenString,
                oid: item.oid
            },
        }).then(res => {
            this.$toast.clear();
            if(res.data.code == 90000){
                this.orderData.splice(index,1);
            }
            
            
        })
    }
  },
};
</script>